import React from "react";
import { Link } from "react-router-dom";
import "./Navbar.css";

const navbar = () => {
  return (
    <nav class="navbar">
      <a class="navbar-brand bold">User Directory</a>
      <form class="form-inline">
        <input
          class="form-control"
          type="search"
          placeholder="Search"
          aria-label="Search"
        ></input>
        <button class="btn btn-outline-primary" type="submit">
          Search
        </button>
      </form>
    </nav>
  );
};

export default navbar;
